import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider } from './context/AuthContext';
import { LibraryProvider, useLibrary } from './context/LibraryContext';
import Navbar from './components/Navbar';
import LoadingScreen from './components/LoadingScreen';
import Home from './pages/Home';
import Movies from './pages/Movies';
import Series from './pages/Series';
import Details from './pages/Details';
// import Player from './pages/Player'; // Desactivat - ara s'utilitza streaming extern
import StreamPlayer from './pages/StreamPlayer';
import Search from './pages/Search';
import Admin from './pages/Admin';
import Books from './pages/Books';
import BookReader from './pages/BookReader';
import Audiobooks from './pages/Audiobooks';
import AudiobookPlayer from './pages/AudiobookPlayer';
import TV from './pages/TV';
import Programs from './pages/Programs';
import Login from './pages/Login';
import Profile from './pages/Profile';
import './App.css';

// Main app content with loading screen
function AppContent() {
  const { initialLoading, loadingProgress, preloadData } = useLibrary();

  // Preload data on mount
  useEffect(() => {
    preloadData();
  }, [preloadData]);

  if (initialLoading) {
    return <LoadingScreen progress={loadingProgress} />;
  }

  return (
    <Router>
      <div className="app">
        {/* Animated Background */}
        <div className="animated-bg">
          <div className="bubble bubble-1"></div>
          <div className="bubble bubble-2"></div>
          <div className="bubble bubble-3"></div>
        </div>

        <Routes>
          {/* Player local desactivat - redirigir a home */}
          <Route path="/play/:type/:id" element={<Navigate to="/" replace />} />

          {/* Stream Player for external sources */}
          <Route path="/stream/:type/:tmdbId" element={<StreamPlayer />} />

          {/* Book Reader without Navbar */}
          <Route path="/read/:id" element={<BookReader />} />

          {/* Audiobook Player without Navbar */}
          <Route path="/listen/:id" element={<AudiobookPlayer />} />

          {/* Login without Navbar */}
          <Route path="/login" element={<Login />} />

          {/* All other routes with Navbar */}
          <Route
            path="*"
            element={
              <>
                <Navbar />
                <main className="main-content">
                  <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/movies" element={<Movies />} />
                    <Route path="/series" element={<Series />} />
                    <Route path="/books" element={<Books />} />
                    <Route path="/audiobooks" element={<Audiobooks />} />
                    <Route path="/tv" element={<TV />} />
                    <Route path="/programs" element={<Programs />} />
                    <Route path="/movies/:id" element={<Details />} />
                    <Route path="/series/:id" element={<Details />} />
                    <Route path="/search" element={<Search />} />
                    <Route path="/admin" element={<Admin />} />
                    <Route path="/profile" element={<Profile />} />
                  </Routes>
                </main>
              </>
            }
          />
        </Routes>
      </div>
    </Router>
  );
}

function App() {
  return (
    <AuthProvider>
      <LibraryProvider>
        <AppContent />
      </LibraryProvider>
    </AuthProvider>
  );
}

export default App;
